<template>
  <v-card 
    style="display:flex;flex-wrap:wrap;min-height:50px;min-height:200px;"
  >
      <template v-if="isLoading">
        <div class="loader" style="background-image:url('assets/loader.gif')"></div>
      </template>
      <template v-else>
        <v-card 
          style="padding:5px;box-sizing:border-box;flex: 1 1 0px"
          cols="4"
          v-for="deck in decks"
          :key="deck.deckId"
        >
            <v-btn :to="`/deck?code=${deck.deckCode}`" class="deck" :style="`flex-basis:0;width:100%;min-width:200px;height:260px;background-image:url('assets/${deck.signatureChampion.code}-full.png');background-position:50% 0;background-size:cover;padding:0px`">
              <h2>{{ deck.archetypeName }}</h2>
              <v-row align-content="center" justify="center" style="padding-right:20px;;position:absolute;bottom:120px;width:100%;box-sizing:border-box">
                <v-col style="padding:0; padding-top:20px" cols="3" v-for="region in deck.factions" :key="region.index">
                  <v-avatar size="62" >
                    <v-img :src="factionImg[region]"/>
                  </v-avatar>
                </v-col>
              </v-row>
              <v-row align-content="center" justify="center" style="padding-right:10px;position:absolute;bottom:60px;width:100%;box-sizing:border-box">
                <v-col style="padding:0;padding-top:20px" cols="2" v-for="champion in deck.champions" :key="champion.index">
                  <v-avatar size="44">
                    <v-img :src="`assets/${champion.code}-full.jpg`" style="border:2px solid #000"/>
                  </v-avatar>
                </v-col>
              </v-row>
              <v-row style="margin-top:15px;position:absolute;bottom:10px;width:100%" align-content="center" justify="center">
                <v-col style="padding:0;padding-top:10px;text-align:center" cols="6">
                  <span>
                    {{deck.winrate}}% WR
                    <br/>
                    {{deck.games}} games
                  </span>
                </v-col>
              </v-row>
            </v-btn>
        </v-card>
      </template>
  </v-card>
</template>

<script>
/*eslint no-console: ["error", { allow: ["log"] }] */
  //import Cards from "./Cards.vue";
  import * as get from "../../utils/get.js";

  export default {
    components: {
        //Cards
    },
    props: {
        filterProp: { type: String },
        timeFrameProp: { type: Number },
        minGamesProp: { type: Number },
        orderProp: { type: Boolean }
    },
    data () {
      return {
        factionImg: {
          IO: "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Ionia.png",
          PZ: "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/PitoverZaun.png",
          DE: "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Demacia.png",
          NX: "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Noxus.png",
          FR: "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Freljord.png",
          SI: "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/ShadowIsles.png",
        },
        headers: [
          { text: 'Archetype', value: 'archetypeName', sortable: false },
          { text: 'Winrate', value: 'winrate' },
          { text: 'Games', value: 'games' },
          { text: 'Cards', value: 'cardsInDeck', sortable: false }
        ],
        decks: [],
        isLoading: false,
        filter: "",
        timeFrame: 30,
        minGames: 1,
        sort: "games",
        // Kasai - sortOrder: 1 = DESC / sortOrder: -1 = ASC
        sortOrder: 1
      }
    },
    mounted () {
      this.getListDecks();
    },
    methods: {
      async getListDecks (filter = "", timeFrame = 30) {
        this.isLoading = true;
        let listDecks = await get.getDecks(filter, timeFrame);
        listDecks.data.forEach(async (deck) => {
          if(!deck.archetypeName){
            if(deck.cardsInDeck.length > 22){
              deck.archetypeName = "Expedition deck";
            }
            else deck.archetypeName = "Offmeta Archetype";
          }
          deck.games = deck.wins+deck.losses;
          deck.games ? deck.winrate = Math.round(deck.wins/(deck.games)*10000)/100 : deck.winrate = 0;
          deck.winrate = parseInt(deck.winrate.toFixed(2));
          deck.champions = [];
          deck.cardsInDeck.forEach(card => {
            if(card.card.rarity === "Champion"){
              deck.champions = [...deck.champions, {code: card.cardCode, cost: card.card.cost}];
            }
          });
          deck.signatureChampion = deck.champions.length ? deck.champions.reduce(function(prev, current) {
              return (prev.cost > current.cost) ? prev : current
          }) : null;
        });
        let onlyValidDecks = [];
        listDecks.data.forEach((deck) => {
          if(deck.cardsInDeck.length > 13){
            onlyValidDecks = [...onlyValidDecks, deck];
          }
        })
        this.decks = onlyValidDecks.sort((a, b) => (a[this.sort] > b[this.sort]) ? -(this.sortOrder) : this.sortOrder);
        this.isLoading = false
      }
    },
    watch: {
      filterProp: {
        handler: function(val){
          this.filter = val
          this.getListDecks(val, this.timeFrame, this.minGames)
        },
        deep: true
      },
      timeFrameProp: {
        handler: function(val){
          console.log(val);
          this.getListDecks(this.filter, val, this.minGames);
        },
        deep: true
      },
      minGamesProp: {
        handler: function(val){
          this.getListDecks(this.filter, this.timeFrame, val);
        },
        deep: true
      },
      orderProp: {
        handler: function(val){
          if(!val){
            this.sort = "winrate";
            this.decks = this.decks.sort((a, b) => (a[this.sort] > b[this.sort]) ? -(this.sortOrder) : this.sortOrder);
          }
          else this.sort = "games";
          this.decks = this.decks.sort((a, b) => (a[this.sort] > b[this.sort]) ? -(this.sortOrder) : this.sortOrder);
        },
        deep: true
      }
    }
  }
</script>
<style>
  .deck {
    border: 1px solid #535353;
    box-shadow: 0px 2px 7px rgba(0,0,0,0.8) !important;
  }
  .deck .v-btn__content {
    display:block;
    width:100%;
    height:260px;
    background-image: linear-gradient(to bottom, rgba(38,50,56,0), rgba(8,4,15,1) 90%);
  }
  .deck h2, .deck .wr-row {
    background: rgba(0,0,0,0.25);
    font-size: 1rem !important;
    padding: 12px 20px !important;
    white-space: break-spaces !important;
    text-align:center;
  }
  .deck .row {
    margin-left: 0px !important;
    margin-right: 0px !important;
  }
</style>