<template>
  <v-app>
    <Navbar />
    <v-data-table
      :headers="headers"
      :items="games"
      multi-sort
      class="elevation-1 replays-table"
      style="height:100%;position:relative;z-index:1"
      :loading="isLoading"
      items-per-page="15"
    >
      <template v-slot:item.playerName="{ item }">
          <template v-if="item.playerWon === true">
            <v-chip class="winner">
              {{ item.playerName }}
              <v-icon right style="font-size:14px">mdi-chess-queen</v-icon>
            </v-chip>
          </template>
          <template v-else>
            <v-chip class="loser">
              {{ item.playerName }}
              <v-icon right style="font-size:14px">mdi-skull</v-icon>
            </v-chip>  
          </template>
          <v-chip class="sword-cross"><v-icon>mdi-sword-cross</v-icon></v-chip>
      </template>
      <template v-slot:item.opponentName="{ item }">
          <template v-if="item.playerWon === false">
            <v-chip class="winner">
              <v-icon left style="font-size:14px">mdi-chess-queen</v-icon>
              {{ item.opponentName }}
            </v-chip>
          </template>
          <template v-else>
            <v-chip class="loser">
              <v-icon left style="font-size:14px">mdi-skull</v-icon>
              {{ item.opponentName }}
            </v-chip>  
          </template>
      </template>
      <template v-slot:item.showReplay="{ item }">
          <v-btn :to="`/replay?id=${item.gameId}`" color="blue-grey darken-3" style="font-size:10px;font-weight:bold">
              <v-icon style="font-size:16px;margin-right:6px;margin-top:-1px">mdi-play</v-icon> replay
          </v-btn>
      </template>
    </v-data-table>
  </v-app>
</template>

<script>
/*eslint no-console: ["error", { allow: ["log"] }] */
import Navbar from "../home/Navbar.vue";
import * as get from "../../utils/get.js";

export default {
  components: {
    Navbar
  },
  data() {
    return {
      games: [],
      isLoading: false,
      headers: [
          { text: '', value: '', sortable: false },
          { text: 'Player', value: 'playerName', sortable: false },
          { text: 'Opponent', value: 'opponentName', sortable: false },
          { text: '', value: 'showReplay', sortable: false },
          { text: '', value: '', sortable: false },
      ]
    }
  }, 
  methods: {
    replaceWinner(player){
      return player.replace("*", (<span class='winner'>WON</span>));
    }
  },
  async mounted() {
    this.isLoading = true;
    let games = await get.getGames();
    let gamesWinner = games.data.map(game => {
      // game.playerName = game.playerWon ? `${game.playerName} *` : game.playerName;
      // game.opponentName = !game.playerWon ? `${game.opponentName} *` : game.opponentName;
      return game;
    });
    this.games = gamesWinner;
    this.isLoading = false;
  } 
};
</script>
<style>
  .winner, .loser {
    font-weight: bold;
    color:#fff !important;
    max-width:168px;
    background-color: transparent !important;
  }
  .loser {
    color: #999 !important;
  }
  .replays-table {
    background-color: rgba(0,0,0,0.2) !important;
  }
  .replays-table col:nth-child(2), .replays-table col:nth-child(3) {
    width: 200px;
  }
  .replays-table col:nth-child(4) {
    width: 200px;
  }
  .replays-table td:nth-child(2), .replays-table th:nth-child(2) {
    text-align:right !important;
    padding-right: 40px;
  }
  .sword-cross {
    position: absolute !important;
    padding: 8px !important;
    margin-left: 10px !important;
  }
  .sword-cross .v-icon {
    font-size:18px !important;
  }
</style>
