<template>
  <v-row>  
      <v-col :style="filterSidebarStyle">
        <v-navigation-drawer 
          style="z-index:3"
          ref="navbar"
          class="filter-sidebar"
        >
            <v-list
            dense
            nav
            class="overflow-y-auto"
            >
                <v-toolbar-title>Player's Regions</v-toolbar-title>
                <v-slide-group
                  v-model="filter.playersFaction"
                  multiple
                  class="factions"
                  @change="applyFilter"
                  max=2
                >
                  <v-slide-item
                    v-for="n in 6"
                    :key="n"
                    v-slot:default="{ active, toggle }"
                  >
                    <v-card
                      :color="active ? 'primary' : 'dark'"
                      class="ma-1"
                      height="28"
                      width="28"
                      @click="toggle"
                      :img="factionImg[n-1]"
                    >
                      <v-row
                        class="fill-height"
                        align="center"
                        justify="center"
                      >
                        <v-scale-transition>
                          <v-icon
                            v-if="active"
                            color="white"
                            size="16"
                            v-text="'mdi-close-circle-outline'"
                          ></v-icon>
                        </v-scale-transition>
                      </v-row>
                    </v-card>
                  </v-slide-item>
                </v-slide-group>
                <v-divider />
                <v-toolbar-title>Opponent's Regions</v-toolbar-title>
                <v-slide-group
                  v-model="filter.opponentsFaction"
                  multiple
                  class="factions"
                  @change="applyFilter"
                  max=2
                >
                  <v-slide-item
                    v-for="n in 6"
                    :key="n"
                    v-slot:default="{ active, toggle }"
                  >
                    <v-card
                      :color="active ? 'primary' : 'dark'"
                      class="ma-1"
                      height="28"
                      width="28"
                      @click="toggle"
                      :img="factionImg[n-1]"
                    >
                      <v-row
                        class="fill-height"
                        align="center"
                        justify="center"
                      >
                        <v-scale-transition>
                          <v-icon
                            v-if="active"
                            color="white"
                            size="16"
                            v-text="'mdi-close-circle-outline'"
                          ></v-icon>
                        </v-scale-transition>
                      </v-row>
                    </v-card>
                  </v-slide-item>
                </v-slide-group>
                <v-divider />
                <v-toolbar-title>Time frame</v-toolbar-title>
                <v-col>
                  <v-slider
                    v-model="filter.timeFrame"
                    class="align-center"
                    :max="90"
                    :min="1"
                    hide-details
                    @change="applyFilter"
                  />
                  <v-row style="max-width: 130px;margin-left: 30px;">
                    <v-col style="padding:0;padding-left:20px">
                      <v-text-field
                        v-model="filter.timeFrame"
                        hide-details
                        single-line
                        type="tel"
                        @change="applyFilter"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="2" style="padding:0;padding-left:10px;padding-top:3px">
                      <span>days</span>
                    </v-col>
                  </v-row>
                </v-col>
                <v-divider />
                <v-toolbar-title>Including cards</v-toolbar-title>
                <v-autocomplete
                  v-model="filter.cardsSelected"
                  :items="cards"
                  filled
                  chips
                  color="blue-grey lighten-2"
                  label="Select"
                  item-text="name"
                  item-value="name"
                  multiple
                  style="padding-left:20px;padding-right:20px"
                  :loading="false"
                  v-on:change="includedChange"
                >
                  <template v-slot:selection="data">
                    <v-chip
                      v-bind="data.attrs"
                      :input-value="data.selected"
                      close
                      @click="data.select"
                      @click:close="remove(data.item)"
                    >
                      <v-avatar left>
                        <v-img :src="'assets/'+data.item.cardCode+'-full.jpg'"></v-img>
                      </v-avatar>
                      {{ data.item.name }}
                    </v-chip>
                  </template>
                  <template v-slot:item="data">
                    <template v-if="typeof data.item !== 'object'">
                      <v-list-item-content v-text="data.item"></v-list-item-content>
                    </template>
                    <template v-else>
                      <v-list-item-avatar>
                        <img :src="'assets/'+data.item.cardCode+'-full.jpg'">
                      </v-list-item-avatar>
                      <v-list-item-content>
                        <v-list-item-title v-html="data.item.name"></v-list-item-title>
                      </v-list-item-content>
                    </template>
                  </template>
                </v-autocomplete>
                <v-divider />
                <v-toolbar-title>Excluding cards</v-toolbar-title>
                <v-autocomplete
                  v-model="filter.excludedCardsSelected"
                  :items="excludedCards"
                  filled
                  chips
                  color="blue-grey lighten-2"
                  label="Select"
                  item-text="name"
                  item-value="name"
                  multiple
                  style="padding-left:20px;padding-right:20px"
                  :loading="false"
                  v-on:change="excludedChange"
                >
                  <template v-slot:selection="data">
                    <v-chip
                      v-bind="data.attrs"
                      :input-value="data.selected"
                      close
                      @click="data.select"
                      @click:close="removeExcluded(data.item)"
                    >
                      <v-avatar left>
                        <v-img :src="'assets/'+data.item.cardCode+'-full.jpg'"></v-img>
                      </v-avatar>
                      {{ data.item.name }}
                    </v-chip>
                  </template>
                  <template v-slot:item="data">
                    <template v-if="typeof data.item !== 'object'">
                      <v-list-item-content v-text="data.item"></v-list-item-content>
                    </template>
                    <template v-else>
                      <v-list-item-avatar>
                        <img :src="'assets/'+data.item.cardCode+'-full.jpg'">
                      </v-list-item-avatar>
                      <v-list-item-content>
                        <v-list-item-title v-html="data.item.name"></v-list-item-title>
                      </v-list-item-content>
                    </template>
                  </template>
                </v-autocomplete>
                <v-divider />
                <v-toolbar-title>Sample size</v-toolbar-title>
                <v-col>
                  <v-slider
                    v-model="filter.sampleSize"
                    class="align-center"
                    :max="10000"
                    :min="1"
                    step="1"
                    hide-details
                    @change="applyFilter"
                  />
                  <v-row style="max-width: 200px;margin-left: 40px;">
                    <v-col cols="4" style="padding:0;padding-left:20px">
                      <v-text-field
                        v-model="filter.sampleSize"
                        hide-details
                        single-line
                        type="tel"
                        @change="applyFilter"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="2" style="padding:0;padding-left:10px;padding-top:3px">
                      <span>games</span>
                    </v-col>
                  </v-row>
                </v-col>
                <v-divider />
            </v-list>
        </v-navigation-drawer>
      </v-col>
      <v-col style="padding: 0px;margin-right: 0px;z-index:2">
        <Navbar :filterSidebar="toggleFilterSidebar" :statsSidebar="toggleStatsSidebar" filterTitle="Filter" filterIcon="tune" />
        <v-row style="margin-bottom:10px;width:100%;padding-left: 18px;padding-top: 5px;">
          <v-card-title style="padding-top:3px;padding-bottom:0px">Legends of Runeterra Decks</v-card-title>
          <v-btn 
              color="black"
              class="order"
            >
            <v-switch
              color="#ffffff"
              v-model="order"
              :label="`Order by ${order ? 'sample size' : 'win rate'}`"
            ></v-switch>
          </v-btn>
        </v-row>
        <v-row  style="z-index:2;padding-left: 20px !important;position: relative;width: 100%;" align-content="center" justify="center">
          <v-col class="decks-list" style="padding: 0px 10px 0px 0px !important;height: calc(100vh - 110px);overflow-y: scroll;">
            <Decks 
              :filterProp="filterString" 
              :timeFrameProp="Number(filter.timeFrame)" 
              :minGamesProp="filter.sampleSize" 
              :orderProp="order"
            />
          </v-col>
        </v-row>
      </v-col>
      <v-col :style="statsSidebarStyle">
        <v-navigation-drawer 
          style="z-index:3"
          relative
          right
          ref="navbar"
        >
          <Stats />
        </v-navigation-drawer>
      </v-col>
    </v-row>
</template>

<script>
/*eslint no-console: ["error", { allow: ["log"] }] */
  import * as get from "../../utils/get.js";
  import Decks from "./Decks.vue";
  import Stats from "./Stats.vue";
  import Navbar from "./Navbar.vue";

  export default {
    components: {
      Decks,
      Stats,
      Navbar
    },
    data () {
      return {
        factionImg: [
          "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Ionia.png",
          "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/PitoverZaun.png",
          "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Demacia.png",
          "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Noxus.png",
          "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Freljord.png",
          "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/ShadowIsles.png",
        ],
        factionNames: [
          "IO", "PZ", "DE", "NX", "FR", "SI" 
        ],
        items: [
          { title: 'Dashboard', icon: 'mdi-view-dashboard' },
          { title: 'Photos', icon: 'mdi-image' },
          { title: 'About', icon: 'mdi-help-box' },
        ],
        right: null,
        cards: [],
        excludedCards: [],
        filter: {
          playersFaction: [],
          opponentsFaction: [],
          timeFrame: 30,
          cardsSelected: [],
          excludedCardsSelected: [],
          sampleSize: 1,
        },
        filterString: "",
        filterSidebar: true,
        filterSidebarStyle: {
          padding: "0px",
          position: "relative",
          marginLeft: "0px",
          maxWidth: "280px",
          height: "100vh",
          zIndex: 2
        },
        statsSidebar: true,
        statsSidebarStyle: {
          padding: "0px",
          position: "relative",
          marginRight: "0px",
          maxWidth: "280px",
          height: "100vh",
          zIndex: 2,
          overflow: "scroll"
        },
        order: false
      }
    },
    
    watch: {
      isUpdating (val) {
        if (val) {
          setTimeout(() => (this.isUpdating = false), 3000)
        }
      },
    },

    mounted() {
      this.getListCards();
    },

    methods: {
      toggleFilterSidebar () {
        if(this.filterSidebar){
          this.filterSidebarStyle.marginLeft = "-267px";  
        }
        else {
          this.filterSidebarStyle.marginLeft = "0px";  
        }
        this.filterSidebar = !this.filterSidebar;
      },
      toggleStatsSidebar () {
        if(this.statsSidebar){
          this.statsSidebarStyle.marginRight = "-270px";  
        }
        else {
          this.statsSidebarStyle.marginRight = "0px";  
        }
        this.statsSidebar = !this.statsSidebar;
      },
      remove (item) {
        const index = this.filter.cardsSelected.indexOf(item);
        if (index >= 0) this.filter.cardsSelected.splice(index, 1);
        this.applyFilter();
      },
      removeExcluded (item) {
        const index = this.filter.excludedCardsSelected.indexOf(item);
        if (index >= 0) this.filter.excludedCardsSelected.splice(index, 1);
        this.applyFilter();
      },
      async getListCards () {
        let listCards = await get.getCards(500);
        this.cards = listCards.data;
        this.excludedCards = listCards.data;
      },
      includedChange (value) {
        value.map((included) => {
          this.removeExcluded(included);
        });
        this.applyFilter();
      },
      excludedChange (value) {
        value.map((included) => {
          this.remove(included);
        });
        this.applyFilter();
      },
      async applyFilter () {
        let filter = {
          playersFaction: [],
          opponentsFaction: [],
          includedCards: [],
          excludedCards: []
        };
        filter.playersFaction = this.filter.playersFaction;
        filter.includedCards = this.filter.cardsSelected;
        filter.excludedCards = this.filter.excludedCardsSelected;
        let filterJSON = [];
        await Promise.all(filter.includedCards.map(async (card) => {
          let getCard = await get.getCard(card, "name");
          let cardCode = getCard.cardCode.substring(0,7);
          filterJSON = [...filterJSON, {field: "cardsInDeck.cardCode", value: cardCode}];
        })).then(filterJSON => {
          return filterJSON;
        });
        await Promise.all(filter.excludedCards.map(async (card) => {
          let getCard = await get.getCard(card, "name");
          let cardCode = getCard.cardCode.substring(0,7);
          filterJSON = [...filterJSON, {field: "cardsInDeck.cardCode", op: "Ne", value: cardCode}];
        })).then(filterJSON => {
          return filterJSON;
        });
        await Promise.all(filter.playersFaction.map(async (faction) => {
          filterJSON = [...filterJSON, {field: "factions", value: this.factionNames[faction]}];
        })).then(filterJSON => {
          return filterJSON;
        });
        //filterJSON = [...filterJSON, {field: "wins", op: "Gte", value: this.filter.sampleSize}];
        this.filterString = JSON.stringify(filterJSON);
        console.log(this.filterString);
      }
    },
  }
</script>

<style>
    .v-subheader {
        height: 1rem !important;
        margin-top: 1rem;
    }
    .factions .v-image {
      width:32px;
      height:32px;
    }
    .factions .v-slide-group__prev, .factions .v-slide-group__next {
      display:none;
    }
    .filter-bg .v-image__image {
      background-position:31% !important;
    }
    .v-slide-group__content {
      padding-left: 19px;
      height: 150px;
      width: 200px;
      flex-wrap: wrap;
    }
    .v-slide-group__content > .v-card {
      width: calc(33% - 8px) !important;
      height: 44% !important;
      background-size: contain !important;
      display: inline-block !important;
    }
    .order {
      margin-left: 5px !important;
      width: 235px;
    }
    .order .v-input {
      position: absolute;
      top: -31px;
      left: 0px;
    }
    .order .v-label {
      font-size: 12px;
    }
    .filter-sidebar .v-input {
      padding-top: 0px !important;
    }
    .filter-sidebar .v-input input {
      padding: 0px !important;
      width: 60px !important;
    }
</style>