<template>
    <v-row>
        <v-col>
            <v-toolbar-title>
                Regions Statistics
            </v-toolbar-title>
            <v-card
                style="padding:15px;min-height:150px;"
            >
                <template v-if="isLoading">
                    <div class="loader" style="background-image:url('assets/loader.gif')"></div>
                </template>
                <template v-else>
                    <div 
                        v-for="region in regions"
                        :key="region.index"
                    >
                        <div class="region-name">
                            <v-avatar size="24">
                                <v-img :src="factionImg[region]" />
                            </v-avatar>
                            {{ regionNames[region] }}
                        </div>
                        <v-row class="regions-stats">
                            <v-col cols="4">Wins: {{ factionsStats[region].w }}</v-col>
                            <v-col cols="4">Losses: {{ factionsStats[region].l }}</v-col>
                            <v-col cols="4">W/L: {{ factionsStats[region].wr }}%</v-col>
                        </v-row>
                        <v-progress-linear
                            v-model="factionsStats[region].wr"
                            :color="regionColors[region]"
                            class="region-winrate"
                            height="8"
                            striped
                        ></v-progress-linear> 
                    </div>
                </template>
            </v-card>
            <v-toolbar-title>
                Best performing decks
            </v-toolbar-title>
            <v-card
                style="min-height:150px"
                class="best-decks"
            >
                <template v-if="isLoading">
                    <div class="loader" style="background-image:url('assets/loader.gif')"></div>
                </template>
                <template v-else>
                    <div class="minimum-games">Minimum games on record: {{ minGames }}</div>
                    <v-list>
                        <v-list-item
                            v-for="deck in decks"
                            :key="deck.index"
                            :style="`background-image:url('assets/${deck.signatureChampion.code}-full.jpg')`"
                            class="best-deck"
                            :to="`/deck?code=${deck.deckCode}`"
                        >
                            <div class="best-deck-background"></div>
                            <v-list-item-title class="best-deck-title">
                                {{ deck.archetypeName }}
                            </v-list-item-title>
                            <v-list-item-avatar
                                v-for="champion in deck.champions"
                                :key="champion.index"
                                class="best-deck-avatar"
                            >
                                <v-img 
                                    :src="`assets/${champion.code}-full.jpg`"
                                    class="best-deck-avatar-img"
                                />
                            </v-list-item-avatar>
                        </v-list-item>
                    </v-list>
                </template>
            </v-card>
            <v-toolbar-title>
                Best performing cards
            </v-toolbar-title>
            <v-card
                style="min-height:150px"
                class="best-decks"
            >
                <template v-if="isLoadingCards">
                    <div class="loader" style="background-image:url('assets/loader.gif')"></div>
                </template>
                <template v-else>
                    <div class="minimum-games">Minimum games on record: {{ minGames }}</div>
                    <v-list>
                        <v-list-item
                            v-for="card in cards"
                            :key="card.index"
                            :style="`background-image:url('assets/${card.cardCode}-full.jpg')`"
                            class="best-deck"
                            :to="`/card?code=${card.cardCode}`"
                        >
                            <div class="best-deck-background"></div>
                            <v-list-item-title class="best-deck-title">
                                <b style="margin-right:10px">{{ card.cost }}</b> {{ card.name }}
                            </v-list-item-title>
                            <v-list-item-avatar>
                                <v-img :src="card.regionImg" />
                            </v-list-item-avatar>
                        </v-list-item>
                    </v-list>
                </template>
            </v-card>
            <v-toolbar-title>
                Overall LoRstats records
            </v-toolbar-title>
            <v-card
                class="overall-stats"
            >
                <template v-if="isLoading">
                    <div class="loader" style="background-image:url('assets/loader.gif')"></div>
                </template>
                <template v-else>
                    <v-row>
                        <v-col cols="8">Decks in database:</v-col>
                        <v-col cols="4">{{decksCount}}</v-col>
                    </v-row>
                    <v-row>
                        <v-col cols="8">Games in database:</v-col>
                        <v-col cols="4">{{gamesCount}}</v-col>
                    </v-row>
                    <v-row>
                        <v-col cols="8">Overall winrate of players using LoR Stats</v-col>
                        <v-col cols="4">{{overallWinrate}}%</v-col>
                    </v-row>
                </template>
            </v-card>
        </v-col>
    </v-row>
</template>
<script>
/*eslint no-console: ["error", { allow: ["log"] }] */
import * as get from "../../utils/get.js";

export default {
    data () {
        return {
            decks: [],
            regions: [],
            regionNames: {
                SI: "Shadow Isles",
                FR: "Freljord",
                DE: "Demacia",
                IO: "Ionia",
                PZ: "Piltover & Zaun",
                NX: "Noxus"
            },
            regionColors: {
                SI: "teal",
                FR: "light-blue",
                DE: "white",
                IO: "pink",
                PZ: "orange",
                NX: "red",    
            },
            factionsStats: {
                SI: { w: 0, l: 0, wr: 0 },
                FR: { w: 0, l: 0, wr: 0 },
                DE: { w: 0, l: 0, wr: 0 },
                IO: { w: 0, l: 0, wr: 0 },
                PZ: { w: 0, l: 0, wr: 0 },
                NX: { w: 0, l: 0, wr: 0 }
            },
            factionImg: {
                IO: "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Ionia.png",
                PZ: "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/PitoverZaun.png",
                DE: "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Demacia.png",
                NX: "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Noxus.png",
                FR: "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Freljord.png",
                SI: "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/ShadowIsles.png"
            },
            isLoading: true,
            isLoadingCards: true,
            minGames: 5,
            cards: [],
            gamesCount: 0,
            decksCount: 0,
            overallWinrate: 0
        }
    },
    async mounted(){
        let decks = await get.getDecks();
        this.decks = decks.data;
        this.decks.map(deck => {
            deck.factions.map(faction => {
                this.factionsStats[faction].w = this.factionsStats[faction].w + deck.wins;
                this.factionsStats[faction].l = this.factionsStats[faction].l + deck.losses;
                this.factionsStats[faction].wr = Math.round(this.factionsStats[faction].w / (this.factionsStats[faction].w + this.factionsStats[faction].l) * 100);
            });
        });
        let regionsSort = [];
        for (let region in this.factionsStats) {
            regionsSort.push([region, this.factionsStats[region].wr]);
        }
        regionsSort.sort(function(a, b) {
            return b[1] - a[1];
        });
        let regionsSorted = [];
        regionsSort.forEach(item => {
            regionsSorted = [ ...regionsSorted, item[0]];
        });
        this.regions = regionsSorted;
        this.decks.forEach(async (deck) => {
          if(!deck.archetypeName){
            if(deck.cardsInDeck.length > 22){
              deck.archetypeName = "Expedition deck";
            }
            else deck.archetypeName = "Offmeta Archetype";
          }
          deck.games = deck.wins+deck.losses;
          deck.games ? deck.winrate = Math.round(deck.wins/(deck.games)*10000)/100 : deck.winrate = 0;
          deck.winrate = parseInt(deck.winrate.toFixed(2));
          deck.champions = [];
          deck.cardsInDeck.forEach(card => {
            if(card.card.rarity === "Champion"){
              deck.champions = [...deck.champions, {code: card.cardCode, cost: card.card.cost}];
            }
          });
          deck.signatureChampion = deck.champions.length ? deck.champions.reduce(function(prev, current) {
              return (prev.cost > current.cost) ? prev : current
          }) : null;
        });
        this.decksCount = this.decks.length;
        let games = await get.getGames();
        let gamesWinner = 0
        games.data.forEach(game => {
            if(game.playerWon){
                gamesWinner++;
            }
        });
        this.gamesCount = games.total;
        this.overallWinrate = Math.round(gamesWinner / this.gamesCount * 100);
        let onlyValidDecks = [];
        this.decks.forEach((deck) => {
          if(deck.cardsInDeck.length > 13 && deck.games >= this.minGames ){
            onlyValidDecks = [...onlyValidDecks, deck];
          }
        });
        this.decks = onlyValidDecks.sort((a, b) => (a["winrate"] > b["winrate"]) ? -1 : 1).slice(0,5);
        this.isLoading = false;
        this.isLoadingCards = true;
        let listCards = await get.getCards(500, "", true);
        listCards.data.forEach((card) => {
          card.games = card.wins+card.losses;
          card.games ? card.winrate = Math.round(card.wins/(card.games)*10000)/100 : card.winrate = 0;
          switch(card.region) {
            case "Shadow Isles":
                card.regionImg = "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/ShadowIsles.png";
                break;
            case "Demacia":
                card.regionImg = "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Demacia.png";
                break;
            case "Piltover & Zaun":
                card.regionImg = "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/PitoverZaun.png";
                break;
            case "Noxus":
                card.regionImg = "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Noxus.png";
                break;
            case "Ionia":
                card.regionImg = "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Ionia.png";
                break;
            case "Freljord":
            default:
                card.regionImg = "https://playruneterra.com/assets/48b7bffe8468c1a99fabdd06f1897e8a709e3343/static/image/faction-icons/Freljord.png";
                break;
          } 
        });
        let onlyValidCards = [];
        listCards.data.forEach((card) => {
          if(card.games >= this.minGames ){
            onlyValidCards = [...onlyValidCards, card];
          }
        })
        this.cards = onlyValidCards.sort((a, b) => (a["winrate"] > b["winrate"]) ? -1 : 1).slice(0,5);
        this.isLoadingCards = false;
        console.log(this.cards);
    } 
}
</script>
<style>
    .regions-stats .col {
        font-size: 12px;
        padding-top: 5px;
        padding-bottom: 0px;
    }
    .region-name {
        font-size: 14px;    
    }
    .region-winrate {
        margin-bottom: 20px;
    }
    .best-deck {
        background-position:50%;
        background-size:cover;
        margin-bottom: 2px;
    }
    .best-deck::before {
        opacity: 0 !important;
    }
    .best-deck-background {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background:rgba(0,0,0,0.5);
    }
    .best-deck-title {
        z-index:3;
        font-size:12px !important;
    }
    .best-deck-avatar {
        margin-left:-10px !important;
        z-index:2
    }
    .best-deck-avatar-img {
        border: 2px solid #000
    }
    .minimum-games {
        font-size: 10px;
        margin-left: 10px;
        margin-top: 3px;
    }
    .best-decks {
        padding: 15px;
    }
    .overall-stats {
        padding: 25px;
    }
    .overall-stats .col {
        font-size: 12px;
    }
    .overall-stats .col:nth-child(2) {
        font-weight: bold;
        font-size: 14px;
    }
</style>